.services {
  padding-top: 40px;
  padding-bottom: 40px;

  background-color: #2b2c30;

  &__heading {
    margin-bottom: 20px;

    color: #f5f5f6;
  }

  &__list {
    margin: 0;
    padding: 0;

    list-style: none;
  }

  &__item:not(:last-child) {
    margin-bottom: 20px;
  }

  &__title {
    margin: 0;

    font-size: 20px;
    line-height: 29px;
    font-weight: 900;
    color: #f5f5f6;
  }

  &__desc {
    margin: 0;
    margin-top: 4px;

    color: #d3d3d5;
  }
}
