.page-header {
  background-color: #eeeeee;
  background-image: url("../img/background-header-mobile@1x.png");
  background-repeat: no-repeat;
  background-position: bottom 34px center;

  &__inner {
    position: relative;
    min-height: 684px;

    padding-top: 18px;
  }

  &__logo img {
    border-radius: 50%;
  }

  &__phone {
    position: absolute;
    top: 19px;
    right: 15px;
    margin: 0;
  }

  &__lead {
    margin: 0;
    margin-bottom: 27px;
    padding-top: 46px;

    font-size: 45px;
    line-height: 51px;
    font-weight: 900;
  }

  &__desc {
    margin: 0;

    font-size: 24px;
    line-height: 32px;
  }
}
