.address {
  margin-bottom: 16px;
  font-style: normal;

  &__list {
    margin: 0;
    margin-bottom: 10px;
    padding: 0;

    list-style-type: none;

    counter-reset: item;
  }

  &__item:before {
    content: counter(item) ') ';
    counter-increment: item;
  }

  &__advantage {
    margin: 0;
  }
}
