.tabs {

  &__list {
    display: flex;
    align-items: center;
  }

  &__item {
    margin-right: 24px;
  }

  &__link {
    padding: 0;
    font-family: inherit;
    font-size: 16px;
    line-height: 22px;
    background-color: #ffffff;
    border: none;
    border-bottom: 1px dotted;
  }

  &__item--active &__link {
    padding: 12px 10px 10px;
    background-color: #eeeeee;
    border: none;
  }

  &__content {
    padding-top: 30px;
    font-size: 18px;
  }
}
