.advantages {
  padding-top: 284px;
  padding-bottom: 50px;

  background-image: url("../img/background-services-mobile@1x.png");
  background-repeat: no-repeat;
  background-position: top 30px center;

  &__heading {
    margin-bottom: 17px;
  }

  &__list {
    margin: 0;
    padding: 0;

    list-style: none;
  }

  &__item {
    position: relative;
    margin-bottom: 11px;
    padding-left: 36px;

    &:before {
      content: '';
      position: absolute;
      left: 0;
      width: 20px;
      height: 20px;
      background-color: #2bba53;
    }

  }
}
