.rates {
  padding-bottom: 50px;

  &__heading {
    margin-bottom: 12px;
  }

  &__intro {
    margin: 0;
    margin-bottom: 20px;

    font-size: 20px;
    line-height: 28px;
  }

  &__list {
    margin-bottom: 30px;
  }

  &__item {
    margin-bottom: 16px;
    padding: 25px 20px 30px 20px;

    border-radius: 10px;

    &--express {
      background: linear-gradient(180deg, #b5ffa3 0%, rgba(255, 255, 255, 0) 100%), #cafba4;
    }

    &--standart {
      border: 1px solid #d8d8d8;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 20px;

    font-size: 24px;
    line-height: 34px;
    font-weight: 800;
  }

  &__desc {
    margin: 0;

    font-size: 20px;
    line-height: 28px;
  }
}
